@import "../variables";

.header {
    border-bottom: 1px solid $gray-line;
    padding: 40px 0;

    .container {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media (max-width: 645px) {
            flex-direction: column;
            justify-content: center;
            .header__logo {
                margin-bottom: 20px;
            }
        }
    }

    &__logo {
        display: flex;
        justify-content: center;
        align-items: flex-end;

        img {
            margin-right: 15px;
        }

        h1 {
            color: #181818;
            font-size: 24px;
            letter-spacing: 1%;
            text-transform: uppercase;
            font-weight: 800;
        }

        span {
            color: #7b7b7b;
            margin-left: 10px;
            font-style: italic;
            font-size: 20px;
            @media (max-width: 900px) {
                display: none;
            }
        }
    }
}
